<template>
  <div class="settings-index-page">
    <h1>关于</h1>

    <div class="about-info-box">
      <div><img class="wpm-icon" src="/image/icon_wpm.png" /></div>
      <h3>虫洞项目管理器</h3>

      <el-form label-width="100px">
        <el-form-item label="版本">
          {{ about.version }}
          <CheckUpdateButton class="check-update-button" />
        </el-form-item>

        <el-form-item label="版权"> {{ about.copyright }} </el-form-item>

        <el-form-item label="操作系统"> {{ about.os }} </el-form-item>
        <el-form-item label="处理器"> {{ about.arch }} </el-form-item>
        <el-form-item label="运行模式"> {{ about.profile }} </el-form-item>
      </el-form>

      <div v-show="!displayMore">
        <el-button type="primary" link @click="displayMore = true">
          更多
        </el-button>
      </div>

      <el-form label-width="100px" v-show="displayMore">
        <el-form-item label="用户文件夹"> {{ about.home }} </el-form-item>
        <el-form-item label="Web 界面"> {{ about.weburl }} </el-form-item>
        <el-form-item label="SHA256" :title="about.sha256sum">
          {{ makeShortSum(about.sha256sum) }} ...
        </el-form-item>
      </el-form>

      <div class="the-more-info-box" v-show="displayMore">
        <el-table :data="modules">
          <el-table-column prop="name" label="模块名称">
            <template #default="scope">
              <div :title="scope.row.hex_name">{{ scope.row.name }}</div>
            </template>
          </el-table-column>

          <el-table-column
            prop="name"
            label=""
            width="100"
            :formatter="formatIsMain"
          >
            <!-- 主模块 -->
          </el-table-column>

          <el-table-column
            prop="version"
            label="版本"
            width="150"
          ></el-table-column>
          <el-table-column
            prop="revision"
            label="修订次序"
            width="100"
          ></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
    
<script>
import CheckUpdateButton from "@/components/checkupdate/check-update-button.vue";

function nop() {}

export default {
  name: "settings-about",

  components: { CheckUpdateButton },

  data() {
    return {
      about: {
        name: "",
        version: "",
      },
      modules: [],
      mainModule: {},
      displayMore: false,
    };
  },

  methods: {
    fetch() {
      let p = {
        method: "GET",
        type: "about",
      };
      this.$store.dispatch("rest/execute", p).then((res) => {
        let vo = res.data;
        this.about = vo;
        this.modules = vo.modules;
        this.mainModule = vo.main_module;
      });
    },

    formatIsMain(row, column, cellValue, index) {
      nop(row, column, cellValue, index);
      let name1 = cellValue;
      let name2 = this.mainModule.name;
      return name1 == name2 ? "(主模块)" : "";
    },

    makeShortSum(text) {
      const wantLen = 7;
      text = "" + text;
      let len = text.length;
      if (len > wantLen) {
        return text.substring(0, wantLen);
      }
      return text;
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>
  
<style scoped>
.settings-index-page {
  text-align: left;
}

.about-info-box {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.wpm-icon {
  max-width: 96px;
  max-height: 96px;
}

.the-more-info-box {
  width: 90%;
  max-width: 768px;
}

.check-update-button {
  margin-left: 20px;
}
</style>
