<template>
  <div>
    <el-button link type="primary" @click="check"> 检查更新 </el-button>
    <MyNewVersionDialog v-model="displayNewVersion" />
  </div>
</template>

<script>
import { ElLoading, ElMessageBox } from "element-plus";
import MyNewVersionDialog from "./new-version-dialog.vue";

export default {
  name: "check-update-button",

  components: { MyNewVersionDialog },

  data() {
    return {
      displayNewVersion: false,
    };
  },

  methods: {
    check() {
      const loading = ElLoading.service({
        lock: true,
        text: "正在检查更新",
        background: "rgba(0, 0, 0, 0.7)",
      });

      let vo = {
        checkupdate: { force: true },
      };

      this.$store
        .dispatch("checkupdate/check", vo)
        .then((r) => {
          this.showResult(r.data);
        })
        .finally(() => {
          loading.close();
        });
    },

    showResult(vo) {
      let cu = vo.checkupdate;
      if (cu.has_new_version) {
        this.displayNewVersion = true;
      } else {
        ElMessageBox.alert("已是最新版本");
      }
    },
  },
};
</script>
